import logo from "./logo.svg";
import Header from "./components/Header"
import Tasks from './components/Tasks'
import { useState } from "react";
import AddTask from './components/AddTask'

function App() {
  const [showAddTask, setShowAddTask] = useState(false)
  const [tasks, setTasks] = useState([
    {
      id: 1,
      text: "Task 1",
      day: 'Today',
      reminder: true,
    },
    {
      id: 2,
      text: "Task 2",
      day: 'Friday, 5pm',
      reminder: false,
    },
    {
      id: 3,
      text: "Task 3",
      day: 'Sunday, 1pm',
      reminder: false,
    }
  ]);

  // Add Task
  const addTask = (task) => {
    const id = Math.floor(Math.random() * 1000) + 1
    const newTask = { id, ...task }
    setTasks([...tasks, newTask])
    
  }

  // Delete Task
  const deleteTask = (id) => {
    setTasks(tasks.filter((task) => task.id !== id))
  }

  // Toggle Reminder 
  const toggleReminder = (id) => {
    setTasks(tasks.map((task) => task.id === id ? { ...task, reminder: !task.reminder } : task))
  }

  return (
    <div className="container">
      <Header onAdd={() => setShowAddTask(!showAddTask)} />
      {/* showAddTask === true ? <AddTask onAdd={addTask} : ''*/}
      {/* It could also be written with logical operator && */}
      {/* && - is a short way of writing ternary operator without false statement */}
      {showAddTask && <AddTask onAdd={addTask} />}
      {
      tasks.length > 0 ? <Tasks tasks={tasks} onDelete={deleteTask} onToggle={toggleReminder} /> : "No tasks, please add one"
      }
    </div>
  );
}

export default App;
