import React from 'react'
import { FaTimes } from 'react-icons/fa'

// onClick=onDelete(task.id) - works immediately
// onClick={onDelete} - only works this way
// onClick={() => onDelete(task.id)} - onDelete as callback
// an event handler is supposed to be either a function or a function reference and a onDelete() is calling the function

const Task = ({ task, onDelete, onToggle }) => {
  return (
    <div 
      className={`task ${task.reminder === true ? 'reminder' : ''}`} 
      onDoubleClick={() => onToggle(task.id)}>
      <h3>
        {task.text} <FaTimes style={{ color: 'red', cursor: 'pointer'}} onClick={() => onDelete(task.id)} /> 
      </h3> 
      <p>{task.day}</p>
    </div>
  )
}

export default Task
